
.wrapperInfo {
    display: flex;
    justify-content: flex-start;
}


.wrapperTextInfo{
    display: flex;
    flex-direction: column;
}

.imgAvatar img{
    margin: 0 20px 0 0 ;
}

wrapperStatus{
    display: flex;
    flex-direction: column;
}

.status {
    text-decoration: none;
    color: #154284;
    font-family: Arial,Helvetica Neue,Helvetica,sans-serif;
    font-weight: 400;
    font-style: italic;
    border: none;
    font-size: 1.8rem;
    line-height: 2.2rem;
    background: linear-gradient(90deg,#154284,#154284);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
}
.name{
    background: linear-gradient(90deg, #cd122d, #154284);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    color: #fff;
    font-size: 2rem;
    font-family:  Arial, Helvetica Neue, Helvetica, sans-serif;
}