.postList {
    display: flex;
    flex-direction: column;
}
.wrapperDialogs {
    display: flex;
    flex-direction: column;
    margin-left: 20px;

}
.wrapperTextarea {
    display: flex;
    flex-direction: column;
    margin: 0 0 20px 20px
}
.button {
    text-align: center;
    text-transform: uppercase;
    cursor: pointer;
    font-size: 0.875rem;
    font-family: "Roboto", "Helvetica", "Arial", sans-serif;
    font-weight: 500;
    line-height: 1.75;
    min-width: 64px;
    letter-spacing: 0.02857em;
    position: relative;
    background-color: #154284;
    border: none;
    color: #fff;
    padding: 6px 16px;
    width: 110px;
    transition-duration: 0.4s;
    overflow: hidden;
    box-shadow: 0 5px 15px #193047;
    border-radius: 4px;
    margin: 10px;
}

.button:hover {
    background: #fff;
    box-shadow: 0 2px 10px 5px #154284;
    color: #000;
}

.button:after {
    content: "";
    background: #154284;
    display: block;
    position: absolute;
    padding-top: 300%;
    padding-left: 350%;
    margin-left: -20px !important;
    margin-top: -120%;
    opacity: 0;
    transition: all 0.8s
}

.button:active:after {
    padding: 0;
    margin: 0;
    opacity: 1;
    transition: 0s
}

.button:focus {
    outline: 0;
}